.swiper-pagination {
    position: static;
    .swiper-pagination-bullet {
        width: 12px;
        height: 12px;
        background: transparent;
        opacity: 1;
        margin-left: 15px;  
        transition: .4s;      
    }
     &.white {
        .swiper-pagination-bullet {
            border: 1px solid #fff;
            &.swiper-pagination-bullet-active {
                background-color: #fff;
            }
        }
     }
     &.blue {
         .swiper-pagination-bullet {
             border: 1px solid map_get($blue, default);
             &.swiper-pagination-bullet-active {
                 background-color: map_get($blue, default);
             }
         }
     }
}


.swiper-btn-next,
.swiper-btn-prev {
    top: 50%;
    position: absolute;
    transition: transform 300ms, opacity 300ms;
    cursor: pointer;
    z-index: 1;
    background-size: 0;
    &.swiper-button-disabled {
        cursor: default;
    }
    
    &.swiper-btn--brown {
        width: 30px;
        height: 30px;
        margin-top: -15px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNSIgaGVpZ2h0PSIxMiIgdmlld0JveD0iMCAwIDE1IDEyIj48dGl0bGU+QXJ0Ym9hcmQgNTwvdGl0bGU+PHBhdGggZD0iTTcuNSAxMS43bC03LTguOGExLjUgMS41IDAgMSAxIDIuNC0xLjhsNC42IDUuOCA0LjYtNS44YTEuNSAxLjUgMCAxIDEgMi40IDEuOXoiIGZpbGw9IiMwMDAiLz48L3N2Zz4=');
        svg {
            width: 15px;
            height: 12px;
            path {
                fill: map_get($gray, default);
                transition: fill 300ms;
            }
        }

        &:not(.swiper-button-disabled):hover {
            svg path {
                fill: map_get($gray, hover);
            }
        }

        &.swiper-button-disabled {
            opacity: .3;
        }
    }

    &.swiper-btn--brown-backg {
        width: 22px;
        height: 42px;
        margin-top: -21px;
        svg {
            width: 100%;
            height: 100%;
            * {
                transition: fill 300ms;
            }
            & > g > path {
                fill: map_get($gray, default) !important;
            }
            polygon {
                fill: #fff;
            }
        }

        &:not(.swiper-button-disabled):hover {
            svg {
                & > g > path {
                    fill: map_get($gray, default) !important;
                }

                polygon {
                    fill: #fff;
                }
            }
        }

        &.swiper-button-disabled {
            svg {
                & > g > path {
                    fill: #F0ECEB !important;
                }
                polygon {
                    fill: #BDAAA4;
                }
            }
        }
    }
}

.swiper-btn-next {
    &.swiper-btn--brown {
        svg {
            transform: rotate(-90deg);
        }
    }

    &.swiper-btn--brown-backg {
        background-image: url('data:image/svg+xml;base64,');
    }
}

.swiper-btn-prev {
    &.swiper-btn--brown {
        svg {
            transform: rotate(90deg);
        }
    }
    &.swiper-btn--brown-backg {
        background-image: url('data:image/svg+xml;base64,');
    }
}

.full-width-slider {
    .swiper-btn-next {
        display: block;
        &.swiper-btn--brown  {
            right: 0;
        }
    }
    .swiper-btn-next {
        display: block;
    }
}