.rating-stars {
    .rating-stars__n {
        display: inline-block;
        width: 27px;
        height: 26px;
        background: url('data:image/svg+xml;base64,');
        background-size: 0;
        svg {
            width: 100%;
            height: 100%;
        }
        &:not(.fill) {
            svg * {
                fill: #d7d7da;
            }
        }
        &.fill {
            svg * {
                fill: #ffa000;
            }
        }
    }
}