.content-block {
    padding: 50px 45px;
    background-color: #fff;
    @include clearfix;
}

.reviews-slider-block {
    .reviews-slider-wrapper {
        position: relative;
    }
    .reviews-slider {
        .swiper-slide {
            display: flex;
            margin: auto;
        }
        &__pagination {
            width: 100%;
            margin-top: 20px;
        }
    
        &__btn-next {
            right: -30px;
        }
    
        &__btn-prev {
            left: -30px;
        }
    }
}

.reviews-slider-block-wrap {
    .btn-center-block {
        margin: 20px 0;
    }
}
