.page-title {
    color: #2b2b2b;
    font-family: $merri-font;
    font-size: 30px;
    font-weight: 700;
    padding-left: 10px;
    cursor: default;
}

.page-title--blue {
    color: map_get($blue, default);
    font-family: $merri-font;
    text-align: center;
    margin-bottom: 30px;
    font-size: 26px;
    margin-top: 0;
    text-transform: uppercase;
}

.block-title {
    font-family: $merri-font;
    font-size: 26px;
    font-weight: 700;
    color: map_get($blue, default);
}

.block-title--900 {
    @extend .block-title;
    font-weight: 900;
}

.block-title--small {
    @extend .block-title;
    font-size: 26px;
}

section.about {
    padding-left: 10px;
    color: map_get($gray-light, default);
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    max-width: 780px;
    ul {
        margin-left: 17px;
        li {
            list-style: disc;
        }
    }
    .about__title {
        margin-top: 10px;
    }
}

.product-horizontal-line {
    height: 1px;
    display: block;
    float: left;
    background: #ececec;
    margin-top: 45px;
    margin-bottom: 45px;
}

.up-scroll {
    box-shadow: 0 0 0 1px rgba(29, 25, 41, 0.13);
    background-color: #ffffff;
    display: block;
    position: fixed;
    border-radius: 100%;
    bottom: -50px;
    right: 3%;
    cursor: pointer;
    z-index: 1000;
    line-height: 48px;
    text-align: center;
    transition: bottom .3s ease-in-out;
    width: 50px;
    height: 50px;
    &:hover {
        // box-shadow: 0 0 0 1px rgba(29, 25, 41, 0.07);
        svg {
            .st0 {
                fill: map_get($blue, hover);
            }
        }
    }
    &:active {
        // box-shadow: 0 0 0 1px rgba(29, 25, 41, 0.2);
        svg {
            .st0 {
                fill: map_get($blue, active);
            }
        }
    }
    &.show {
        bottom: 50px;
    }
    &__pic {
        width: 26px;
        height: 30px;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -15px;
        margin-left: -13px;
        background-size: 0;
        background-image: url(data:image/svg+xml;base64,);
        svg {
            display: block;
            max-width: 100%;
            max-height: 100%;
            .st0 {
                transition: fill .3s ease;
                fill:map_get($blue, default);
            }
        }
    }
}

.pattern-container {
    background: url(../img/backg.png) #f7f7f7;
    height: 100%;
    border-bottom: 1px solid #b7b7b7;
    margin-bottom: 1px;
}

.backg-container {
    background: #f7f7f7; // padding-top: 30px;
    padding-top: 1px;
    margin-top: -1px;
    padding-bottom: 40px;
}

.cookie-shop-banner {
    &__pic {
        max-width: 100%;
        max-height: 100px;
        display: block;
        object-fit: cover;
    }
}

.notransition {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -o-transition: none !important;
    transition: none !important;
}



.mask {
    position: fixed;
    visibility: hidden;
    z-index: -1;
    opacity: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    transition: visibility 0.9s 0s, opacity 0.9s 0s, z-index 0s 3s;
    &.active {
        z-index: 3;
        visibility: visible;
        opacity: 1;
        transition: visibility 0.6s 0s, opacity 0.6s 0s;
    }
}

.main-menu__list {
    position: relative;
    z-index: 3;
}

.cookie-icons {
    margin: 30px 0;
    font-size: 0;
    text-align: center;
    .cookie-icons-img {
        display: inline-block;
        margin-right: 30px;
        -webkit-filter: grayscale(100%);
        filter: grayscale(100%);
    }
}

.btn-center-block {
    text-align: center;
    width: 100%;
    margin: 20px 0;
}


// some text
.information {
    p {
        color: #2b2b2b;
        line-height: 26px;
        padding: 0;
        margin: 0;
    }
}
        
.text-indent {
    height: 20px;
}


// for input
.required {
    &:after {
        content: "*";
        display: inline-block;
        color: red;
        position: absolute;
        right: 0;
        top: 0;
    }
}
.input-field {
    color: map_get($gray, default);
    font-size: 14px;
    font-weight: 400;
    width: 100%;
    border: none;
    outline: none;
    padding-bottom: 3px;
    padding-right: 30px;
    border-bottom: 1px solid map_get($gray-light, default);
    transition: border .3s;
    &:focus {
        border-bottom: 1px solid map_get($gray-light, active);
    }
    &::-webkit-input-placeholder {
        color: #a6a6a6;
    }
    &--big {
        font-size: 18px;
    }
    &--italic {
        font-style: italic;
    }
}

.form-notice {
    color: #a6a6a6;
    font-size: 12px;
    line-height: 20px;
    span {
        color: red;
    }
}

.main-slider {
    max-height: 426px;
}