.tabs {
    background: #fff;
    max-width: 880px;
    &__list {
        padding-left: 25px;
        padding-top: 30px;
        padding-bottom: 45px;
        padding-right: 40px;
    }
    &__item {
        display: none;
        &.active {
            display: block;
        }

        p {
            margin-bottom: 25px;
            font-size: 16px;
            font-weight: 400;
            margin-top: 0;
        }
        .tabs-title {
            font-weight: 700;
            font-size: 16px;
        }
        ul {
            margin-left: 17px;
            margin-bottom: 20px;
            li {
                list-style: disc;
            }
        }
    }
}

.tabs__controls {
    display: flex;
    // border-top: 1px solid #f0eceb;
    &-item {
        position: relative;
        display: inline-block;
        flex-grow: 1;
        text-align: center;
        border-bottom: 4px solid #ececec;
        &:before {
            content: '';
            width: 0;
            height: 4px;
            background: #ececec;
            position: absolute;
            margin: auto;
            bottom: -4px;
            left: 0;
            right: 0;
            transition: .5s;
        }
        &:hover {
            .tabs__controls-link {
                color: map_get($gray, default);
            }
        }
        &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: #ececec;
            width: 1px;
            height: 70%;
            top: 50%;
            transform: translateY(-50%);
        }
        &:first-child {
            &::after {
                display: none;
            }
        }
        
    }
    &-item.active {
        &:before {
            width: 100%;
            background: map_get($blue, default);
        }
        .tabs__controls-link {
            color: map_get($gray, default);
        }
    }
    &-link {
        display: block;
        color: map_get($gray, active);
        font-size: 16px;
        font-weight: 400;
        padding: 9px 15px;
        transition: .3s;

        .reviews-quant {
            color: #2b2b2b;
            font-size: 16px;
            font-weight: 400;
            padding-left: 3px;
        }
    }
}

.tabs--cart {
    max-width: 100%;
    min-height: 100vh;
    margin-top: 10px;
    background-color: #fff;
    .tabs__controls {
        &-item {
            width: 230px;
            flex-grow: 0;
        }
        &-link {
            text-transform: uppercase;
        }
    }
   /*  .tabs__item {
        position: relative;
    } */
}