.open-prod-controls {
    display: flex;
    justify-content: space-between;
    &__col {
        &.center {
            text-align: center;
            width: 150px;
        }
        .open-prod-controls__to-cart-btn {
            height: 42px; 
            margin-top: 26px;
        }
    }
    &__title {
        color: #a6a6a3;
        font-family: $roboto-font;
        font-size: 14px;
        font-weight: 400;
        margin-bottom: 10px;
    }
    &__price-block {
        font-family: $roboto-font;
        line-height: 42px;
        padding-top: 6px;
    }
    &__price, &__signed-price {
        color: #2b2b2b;
        font-family: inherit;
        font-weight: 700;
        display: inline-block;
    }
    &__price {
        font-size: 28px;
        line-height: 28px;
        vertical-align: top;
    }
    &__signed-price {
        font-size: 18px;
        line-height: 22px;
        vertical-align: bottom;
    }
    &__signed-price-title {
        color: #a6a6a6;
        // font-family: inherit;
        font-weight: 400;
        font-size: 11px;
        font-style: italic;
        display: block;
        line-height: 13px;
        width: 141px;
    }
    .buy-in-click-btn {
        margin: 15px auto 0 auto;
    }
}