/*
    Colorbox Core Style:
    The following CSS is consistent between example themes and should not be altered.
*/

#colorbox,
#cboxOverlay,
#cboxWrapper {
	position: absolute;
	top: 0;
	left: 0;
	// z-index: 9999;
	z-index: 9000;
	overflow: hidden;
	-webkit-transform: translate3d(0, 0, 0);
}

#cboxWrapper {
	max-width: none;
}

#cboxOverlay {
	position: fixed;
	width: 100%;
	height: 100%;
}

#cboxMiddleLeft,
#cboxBottomLeft {
	clear: left;
}

#cboxContent {
	position: relative;
}

#cboxLoadedContent {
	overflow: auto;
	-webkit-overflow-scrolling: touch;
}

#cboxTitle {
	margin: 0;
}

#cboxLoadingOverlay,
#cboxLoadingGraphic {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

#cboxPrevious,
#cboxNext,
#cboxClose,
#cboxSlideshow {
	cursor: pointer;
}

.cboxPhoto {
	float: left;
	margin: auto;
	border: 0;
	display: block;
	max-width: none;
	-ms-interpolation-mode: bicubic;
}

.cboxIframe {
	width: 100%;
	height: 100%;
	display: block;
	border: 0;
	padding: 0;
	margin: 0;
}

#colorbox,
#cboxContent,
#cboxLoadedContent {
	box-sizing: content-box;
	-moz-box-sizing: content-box;
	-webkit-box-sizing: content-box;
}


/*
    User Style:
    Change the following styles to modify the appearance of Colorbox.  They are
    ordered & tabbed in a way that represents the nesting of the generated HTML.
*/

#cboxOverlay {
	background: rgba(134, 134, 134, 0.8);
	opacity: 0.9;
	filter: alpha(opacity=90);
}

#colorbox {
	outline: 0;
}

#cboxContent {
	margin-top: 0;
	overflow: visible;
	background: transparent;
}

.cboxIframe {
	background: rgba(134, 134, 134, 0.8);
}

#cboxError {
	padding: 50px;
	border: 1px solid #ccc;
}

#cboxLoadedContent {
	background: transparent;
	padding: 1px;
}

#cboxLoadingGraphic {}

#cboxLoadingOverlay {
	background: transparent;
}

#cboxTitle {
	position: absolute;
	top: -22px;
	left: 0;
	color: #000;
}

#cboxCurrent {
	position: absolute;
	top: -22px;
	right: 205px;
	text-indent: -9999px;
}


/* these elements are buttons, and may need to have additional styles reset to avoid unwanted base styles */

#cboxPrevious,
#cboxNext,
#cboxSlideshow,
#cboxClose {
	border: 0;
	padding: 0;
	margin: 0;
	overflow: visible;
	text-indent: -9999px;
	width: 20px;
	height: 20px;
	position: absolute;
	top: -20px;
	background: none;
}


/* avoid outlines on :active (mouseclick), but preserve outlines on :focus (tabbed navigating) */

#cboxPrevious:active,
#cboxNext:active,
#cboxSlideshow:active,
#cboxClose:active {
	outline: 0;
}

#cboxPrevious {
	background-position: 0px 0px;
	right: 44px;
}

#cboxPrevious:hover {
	background-position: 0px -25px;
}

#cboxNext {
	background-position: -25px 0px;
	right: 22px;
}

#cboxNext:hover {
	background-position: -25px -25px;
}

#cboxClose {
	background-position: -50px 0px;
	right: 0;
}

#cboxClose:hover {
	opacity: 0.8;
	/*background:url(../../img/icon/close-active.png) no-repeat 0 0;
          background-size: contain;
          background-position: 0 0 !important;*/
}

.cboxSlideshow_on #cboxPrevious,
.cboxSlideshow_off #cboxPrevious {
	right: 66px;
}

.cboxSlideshow_on #cboxSlideshow {
	background-position: -75px -25px;
	right: 44px;
}

.cboxSlideshow_on #cboxSlideshow:hover {
	background-position: -100px -25px;
}

.cboxSlideshow_off #cboxSlideshow {
	background-position: -100px 0px;
	right: 44px;
}

.cboxSlideshow_off #cboxSlideshow:hover {
	background-position: -75px -25px;
}

#cboxLoadingGraphic {
	position: absolute;
	margin: auto;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	width: 15px;
	height: 15px;
	border-radius: 100%;
	box-shadow: 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252);
	-o-box-shadow: 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252);
	-ms-box-shadow: 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252);
	-webkit-box-shadow: 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252);
	-moz-box-shadow: 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252);
	animation: cssload-spin ease infinite 4.6s;
	-o-animation: cssload-spin ease infinite 4.6s;
	-ms-animation: cssload-spin ease infinite 4.6s;
	-webkit-animation: cssload-spin ease infinite 4.6s;
	-moz-animation: cssload-spin ease infinite 4.6s;
}

@keyframes cssload-spin {
	0%,
	100% {
		box-shadow: 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252);
	}
	25% {
		box-shadow: -15px 15px rgb(204, 239, 252), -15px -15px rgb(26, 140, 172), 15px -15px rgba(204, 239, 252, 0.98), 15px 15px rgb(26, 140, 172);
	}
	50% {
		box-shadow: -15px -15px rgb(26, 140, 172), 15px -15px rgb(204, 239, 252), 15px 15px rgb(26, 140, 172), -15px 15px rgb(204, 239, 252);
	}
	75% {
		box-shadow: 15px -15px #dfdfdf, 15px 15px #4f4d49, -15px 15px #dfdfdf, -15px -15px #4f4d49;
	}
}

