.gallery-grid {
    .gallery-grid__item {
        float: left;
        width: 300px;
        height: 50%;
        display: none;
        position: relative;
        &:hover {
            .gallery-grid__item-overlay {
                opacity: 1;
                visibility: visible;
            }
        }
        .gallery-grid__item-overlay {
            width: 100%;
            height: 100%;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            background: rgba(0,0,0,.5);
            cursor: pointer;

            opacity: 0;
            visibility: hidden;
            transition: opacity .5s, visibility .5s;
            .gallery-grid__veiw-btn {
                width: 72px;
                height: 72px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                border-radius: 3px;
                background: rgba(255,255,255,.8);
                &:before {
                    content: '';
                    width: 15px;
                    height: 16px;
                    display: block;
                    background-image: url('../img/icon/lupa.svg');
                    background-position: center;
                    background-size: contain;
                    background-repeat: no-repeat;
                }
                &:after {
                    content: 'смотреть фото';
                    font-family: $roboto-font;
                    color: #2b2b2b;
                    font-size: 12px;
                    font-weight: 400;
                    // width: 40px;
                    text-align: center;
                    display: inline-block;
                    margin-top: 3px;
                }
            }
        }
        .gallery-grid__item-img {
            width: 100%;
            height: 100%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
        }
        &.gallery-grid__item--width2 {
            width: 600px;
        }
        &.gallery-grid__item--height2 {
            height: 100%;
        }

        &[data-state="init"] {}

        &[data-state="start"] {
            display: block;
            animation: start-grid 0.5s;
        }
        &[data-state="move"] {
            transition: top 0.5s, left 0.5s, width 0.5s, height 0.5s;
        }
    }       
}


@keyframes start-grid {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}
