@import "vars";

.pagination {
    padding: 30px 0;
    background: #fff;
    text-align: center;
}
.pagination__list {
    font-size: 0;
    display: inline-block;
    overflow: hidden;
}

.pagination__item {
    display: inline-block;
    font-size: 24px;
    vertical-align: middle;
    &:hover .pagination__link {
        color: map_get($gray, active);
    }
    .pagination__link {
        display: inline-block;
    }
    &:nth-child(7) {
        margin-left: -7px;
    }
    &--prev {
        margin-right: 10px;  
    }
    &--next {
        margin-left: 15px;
    }  
}


.pagination__link--prev,
.pagination__link--next {
    cursor: pointer;
    background-size: 0;
    &.pagination__link--brown {
        width: 30px;
        height: 30px;
        transform: rotate(-90deg);
        background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNSIgaGVpZ2h0PSIxMiIgdmlld0JveD0iMCAwIDE1IDEyIj48dGl0bGU+QXJ0Ym9hcmQgNTwvdGl0bGU+PHBhdGggZD0iTTcuNSAxMS43bC03LTguOGExLjUgMS41IDAgMSAxIDIuNC0xLjhsNC42IDUuOCA0LjYtNS44YTEuNSAxLjUgMCAxIDEgMi40IDEuOXoiIGZpbGw9IiMwMDAiLz48L3N2Zz4=');
        svg {
            width: 15px;
            height: 12px;
            path {
                fill: map_get($gray, default);
                transition: fill 300ms;
            }
        }
    }
    &:hover {
        svg path {
            fill: map_get($gray, hover);
        }
    }
}

.pagination__link--next {
    &.pagination__link--brown {
    transform: rotate(90deg);
    }
}

.pagination__link {
    color:  map_get($gray-light, default);
    padding: 0 7px;
}

.pagination__dots {
    display: inline-block;
    color:  map_get($gray, hover);
    height: 100%;
    vertical-align: bottom;
    line-height: 27px;
    letter-spacing: 7px;
    padding: 0 7px;
}

.pagination__item.active .pagination__link{
    color: #313131;
}
