.add-comment-popup {
    width: 550px;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
    background-color: #ffffff;
    padding: 50px 75px 40px 75px;
    &__title {
        color: #2b2b2b;
        font-family: $frank-font;
        font-size: 24px;
        font-weight: 700;
        line-height: 20px;
        margin-bottom: 30px;
    }
    &__tip {
        color: #2b2b2b;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 30px;
    }
    .main-form {
        text-align: center;
        &__row {
            margin-bottom: 20px;
        }
        .pink-btn {
            width: 180px;
            height: 45px;
            line-height: 45px;
            padding: 0;
            margin-top: 30px;
            position: relative;
            z-index: 2;
        }
        &#uploadForm {
            .main-form__input {
                padding-bottom: 6px;
            }
        }
    }


}