.completion-sing-up-popup {
    width: 500px;
    padding: 35px 65px 25px;
    .completion-sing-up-popup__title {
        font-size: 24px;
    }
    .completion-sing-up-popup__form {
        margin-top: 35px;
        .btn-center-block {
            margin: 40px 0 0;
        }
    }
}

.completion-sing-up-popup__wrap {
    display: inline-block;
    position: relative;
    width: 100%;
    margin-bottom: 27px;
}

.completion-sing-up-popup__notice {
    padding-top: 15px;
    border-top: 1px solid #f3f3f3;
    &-explanation {
        padding-top: 15px;
    }
}
