.prod-photo-slider-container {
    
}

.prod-photo-slider-wrapper {
    position: relative;
    .prod-photo-slider__btn-next {
        right: 0;
    }
    
    .prod-photo-slider__btn-prev {
        left: 0;
    }
}

.prod-photo-slider {
    width: 100%;
    height: 420px;
    margin: 0;
    margin-bottom: 10px;
    .swiper-slide {
        &.open-prod-gallery {
            cursor: pointer;
        }
        .prod-photo-slider__img {
            width: 100%;
            height: 100%;
            display: block;
            object-fit: cover;
        }
    }
}

.prod-photo-preview-slider-wrapper {
    position: relative;
    display: inline-block;
    width: 400px;
    margin-left: 25px;
    .prod-photo-preview-slider {
        width: 100%;
        height: 95px;
        margin: 0;
        .swiper-slide {
            overflow: hidden;
            cursor: pointer;
            &:not(.active) {
                position: relative;
                &:before {
                    content: '';
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background: rgba(255,255,255,0.7);
                    transition: background .3s;
                }
                &:hover {
                    &:before {
                        background: rgba(255, 255, 255, 0.4);
                    }
                }
            }
            .prod-photo-preview-slider__img {
                height: 100%;
                width: 100%;
                display: block;
                -o-object-fit: cover;
                object-fit: cover;
                margin: auto;
            }
        }

    }
    .prod-photo-preview-slider__btn-next {
        right: -25px;
    }

    .prod-photo-preview-slider__btn-prev {
        left: -25px;
    }
}

.prod-video-preview {
    width: 95px;
    height: 95px;
    float: right;
    position: relative;
    border: 1px solid #ececec;
    text-align: center;
    line-height: 85px;
    transition: border .3s;
    &:not(.active) {
        cursor: pointer;
    }
    &.active {
        border: 1px solid map_get($brown, default);
        &:after {
            transform: scale(1.1);
        }
    }
    &:not(.active):hover {
        // border: 1px solid #00acc1;
        &:after {
            transform: scale(1.09);
        }
    }
    &:after {
        content: '';
        width: 40px;
        height: 40px;
        position: absolute;
        top: 18px;
        left: 50%;
        margin-left: -20px;
        background-image: url('../img/icon/video_icon.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        transition: transform .3s;
    }
    .prod-video-preview__title {
        color: map_get($gray, default);
        font-family: $roboto-font;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        vertical-align: bottom;
    }
}

.prod-photo-slider__video {
    width: 100%;
    height: 100%;
    #prod-photo-slider-video {
        width: 100%;
        height: 100%;
    }
    .prod-photo-slider__video-overlay {
        width: 100%;
        height: 100%;
        position: absolute;
        cursor: pointer;
        background: rgba(0,0,0,.5);
        top: 0;
        left: 0;
        transition: background .3s;
        &:hover {
            background: rgba(0, 0, 0, .3);
        }
    }
    .prod-photo-slider__play-btn {
        width: 100px;
        height: 70px;
        position: absolute;
        top: 50%;
        margin-top: -35px;
        left: 50%;
        margin-left: -50px;
        background-image: url(../../img/icon/play.png);
        background-position: center;
        -webkit-background-size: contain;
        background-size: contain;
        background-repeat: no-repeat;
    }
}
