.reviews {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    .reviews__user-data {
        .reviews__user-photo {
            border-radius: 50%;
            // overflow: hidden;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
        }
        .reviews__user-name {
            color: map_get($gray-light, default);
            font-family: $roboto-font;
            font-weight: 400;
            cursor: default;
            text-align: center;
        }
    }
    .reviews__content {
        .reviews__text {
            p {
                color: map_get($gray, default);
                font-family: $roboto-font;
                font-weight: 400;
            }
        }
    }
    &.reviews--small {
        .rating-stars__n {
            width: 15px;
            height: 15px;
        }
        .reviews__user-data {
            max-width: 75px;
            margin-right: 25px;
            .reviews__user-photo {
                width: 40px;
                height: 40px;
            }
            .reviews__user-name {
                margin-top: 5px;
                font-size: 10px;
            }
        }
        .reviews__content {
            width: 400px;
            padding-left: 20px;
        }
        .reviews__text p {
            font-size: 12px;
            margin: 0.5em;
        }
    }
    &.reviews--big {
        .reviews__user-data {
            max-width: 105px;
            .reviews__user-photo {
                width: 83px;
                height: 83px;
            }
            .reviews__user-name {
                margin-top: 15px;
                font-size: 14px;
            }
        }
        .reviews__content {
            width: 460px;
            padding-left: 50px;
        }
        .reviews__text p {
            font-size: 14px;
        }
    }
}