.own-set {
    margin-top: 15px;
    padding-top: 10px;
    padding-bottom: 20px;
    background-color: #f3f3f3;
    &__title {
        line-height: 20px;
    }
    &__wrap-items {
        margin-top: 20px;
        @include clearfix;
    }
    &__item {
        width: 48px;
        float: left;
        &:not(:last-child) {
            margin-right: 25px;
        }
    }
    &__name-wrap {
        width: 100%;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__name {
        color: #a6a6a6;
        font-family: $roboto-font;
        font-size: 12px;
        font-weight: 400;
        line-height: 12px;
        text-align: center;
    }
    &__img {
        width: 100%;
        height: 48px;
        color: #a6a6a6;
        font-family: $roboto-font;
        font-size: 12px;
        font-weight: 400;
        line-height: 12px;
        background: #fff;
        border-radius: 3px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            display: block;
            margin: auto;
        }
    }
    .quant-conrol {
        margin-top: 5px;
    }
}
