.socials {
    &__item {
        display: inline-block;
        margin-right: 3px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        width: 43.5px;
        height: 42.1px;
        display: inline-block;
        background-size: 0;
        svg {
            display: block;
            max-width: 100%;
            max-height: 100%;
            .st1 {
                transition: fill .3s ease;
                fill: map_get($blue, default);
            }
            .st0 {
                transition: fill .3s ease;
                fill: #ffffff;
            }
        }
        &:hover {
            svg {
                .st0 {
                    fill: map_get($blue, hover);
                }
                .st1 {
                    fill: #ffffff;
                }
            }
        }
        &:active {
            svg {
                .st0 {
                    fill: map_get($blue, active);
                }
                .st1 {
                    fill: #ffffff;
                }
            }
        }
        &--insta {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--fb {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--ytube{
            background-image: url(data:image/svg+xml;base64,);
        }
        &--etsy {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--yelp {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--twitter {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--google {
            background-image: url(data:image/svg+xml;base64,);
        }
    }
}