.breadcrumbs {
    margin: 10px 0;
    &--pl {
        padding-left: 10px;
    }
    &__item {
        display: inline-block;
        position: relative;
        padding-left: 7px;
        &:first-child {
            padding-left: 0;
            &::before {
                display: none;
            }
        }
        &:last-child {
            .breadcrumbs__link {
                text-decoration: none;
                cursor: default;
                pointer-events: none;
            }
        }
        &::before {
            content: '/';
            position: absolute;
            display: inline-block;
            font-size: 12px;
            color: #a6a6a6;
            font-weight: 400;
            left: 0;
            top: 3px;
        }
    }
    &__link {
        display: inline-block;
        color: #a6a6a6;
        font-size: 12px;
        font-weight: 400;
        transition: .3s;
        text-decoration: underline;
        
        &:hover {
            text-decoration: none;
        }
    }
    &__text {
        display: inline-block;
        color: #a6a6a6;
        font-size: 12px;
        font-weight: 400;
    }
}