.share-title {
    color: #a6a6a5;
    font-family: $roboto-font;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    display: inline-block;
    margin-right: 5px;
}
.share {
    display: inline-block;
    vertical-align: middle;
    height: 25px;
    &__link {
        display: inherit;
        a {
            display: block;
        }
        svg * {
            transition: fill .3s;
        }
        svg {
            .st1 {
                fill: map_get($gray-light, default);
            }
        }
        &:hover {
            svg .st1 {
                fill: map_get($gray-light, hover);
            }
        }
    }
}