.last-viewed {
    background: #fff;
    @include clearfix;
    &-product {
        float: left;
        width: 300px;
        border-right: 1px solid#ececec;
        background: #fff;
        padding: 30px 40px;
    }
    &-product__pic {
        width: 100%;
        height: 166px;
        display: block;
        // margin: 0 auto;
        margin-bottom: 15px;
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &-product__title {
        display: block;
        color: map_get($blue, default);
        font-size: 16px;
        font-weight: 700;
        line-height: 16px;
        margin-bottom: 20px;
        transition: color .3s;
        &:hover {
            color: map_get($blue, hover);
        }
        &:active {
            color: map_get($blue, active);
        }
    }
    .price-block {
        &-row {
            &:first-child {
                margin-bottom: 10px;
            }
            &.for-signed-up {
                // padding-left: 35px;
                .price-block__title {
                    font-size: 12px;
                    &:first-child {
                        font-size: 14px;
                    }
                }
                .price-block__price {
                    font-size: 16px;
                    font-weight: 400;
                    color: #a6a6a6;
                }
            }
        }
        &__title {
            font-size: 14px;
            font-weight: 400;
            color: #a6a6a6;
            &.price-block__title--new-line {
                display: block;
                padding-left: 15px;
            }
        }
        &__price {
            font-size: 18px;
            font-weight: 700;
            color: map_get($gray, default);
            &.old {
                text-decoration: line-through;
            }
            &.new {
                color: #f44336;
            }
        }
    }
}