.gallery-prod-popup {
    width: calc(100vw - 80px);
    min-width: 1200px;
    height: calc(100vh - 100px);
    min-height: 700px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 60px;

    overflow: hidden;
    .left-block {
        float: left;
        width: calc(100% - 420px);
        height: calc(100% - 40px);
    }
    .prod-options__prod-name {
        margin-bottom: 40px;
    }
    .prod-options.right-block {
        float: right;
        width: 417px;
        padding: 0 40px 0 65px;
        .prod-options__form {
            padding: 0;
            margin-top: 0;
            .prod-options__select-wrap {
                display: flex;
                margin-bottom: 20px;
                justify-content: space-between;
                align-content: center;
                align-items: center;
            }
            .prod-options__select-wrap {
                &:not(:first-child) {
                    margin-left: 0;
                }
                .select2-container {
                    width: 230px !important;
                }
            }
        }
        .open-prod-controls.open-prod-controls--border {
            margin-top: 30px;
            padding: 0;
            border-bottom: none;
        }
        .open-prod-controls {
            flex-wrap: wrap;
            justify-content: space-between;
            .open-prod-controls__to-cart-btn.pink-btn {
                margin-top: 40px;
                width: 100%;
                padding: 0;
            }
            .open-prod-controls__col:first-child {
                 margin-right: 20px;
             }
             .open-prod-controls__col:last-child {
                 flex-basis: 100%;
             }
             .open-prod-controls__signed-price {
                 margin-top: 9px;
             }
         }
    }

    .prod-photo-preview-slider-wrapper {
        width: 95px;
        height: 415px;
        margin-left: 0;
        .prod-photo-preview-slider {
            width: 100%;
            height: 100%;
        }
        .swiper-btn-prev {
            top: -20px;
            left: 50%;
            margin-bottom: 5px;
            transform: translate(-50%, 0) rotate(90deg) ;
        }
        .swiper-btn-next {
            top: 100%;
            right: 50%;
            margin-top: 5px;
            transform: translate(50%, 0) rotate(90deg) ;
        }
        .swiper-wrapper {
            flex-direction: column;
            .swiper-slide {
                margin-bottom: 10px;
                width: 100% !important;
            }
        }
    }

    .prod-photo-preview-left {
        .prod-video-preview {
            float: none;
            margin: 35px 0 0 0;
        }
    }

    .prod-photo-slider-container {
        display: flex;
        height: 100%;
        align-items: center;
        .prod-photo-slider-wrapper {
            width: calc(100% - 220px);
            height: 100%;
            display: flex;
            align-items: center;
            margin: auto 0 auto 80px;
            .prod-photo-slider {
                height: 100%;
                .swiper-slide {
                    background: #fff;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    .prod-photo-slider__img {
                        max-width: 100%;
                        max-height: 100%;
                        width: auto;
                        height: auto;
                    }
                }
            }
            .prod-photo-slider__btn-next {
                right: -30px;
                transform: translate(50%, 0);
            }
            .prod-photo-slider__btn-prev {
                left: -30px;
                transform: translate(-50%, 0);
            }
        }
    }

    .open-prod-controls__signed-price-title {
        width: 120px;
    }
    .open-prod-controls__col .open-prod-controls__to-cart-btn {
        width: 100%;
    }
}
