.insta {
    padding-bottom: 15px;
    &-col-wrap {
        display: flex;
        justify-content: space-between;
        position: relative;
        &::after {
            content: '&';
            display: block;
            position: absolute;
            font-size: 17px;
            font-weight: 400;
            color: map_get($gray, default);
            top: 0;
            right: calc(50% - 3px);
        }
    }
    &__title {
        margin-bottom: 30px;
        text-align: center;
    }
    &__list {
        @include clearfix;
        max-width: 572px;
    }
    &__item {
        float: left;
        max-width: 143px;
        max-height: 143px;
        
    }
    &__link {
        display: inline-block;
        width: 143px;
        height: 143px;
        position: relative;
        transition: .2s;
        /* &::after {
            content: '';
            display: none;
            position: absolute;
            left: 0;
            bottom: 0;
            background-color: rgba(#f06292, .35);
            width: 100%;
            height: 100%;
        } */
        &:hover {
            // &::after {
            //     display: block;
            // }
            opacity: .7;
        }
    }
    &__pic {
        max-width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }
}


.insta-page-link {
    color:  map_get($gray, default);
    font-size: 17px;
    font-weight: 400;
    text-decoration: underline;
    &:hover {
        color:  map_get($gray, default);
    }
    &:active {
        color:  map_get($gray, default);
    }
    &-left {
        text-align: right;
        margin-bottom: 20px;
        .insta-page-link {
            padding-right: 15px;
        }
    }
    &-right {
        text-align: left;
        margin-bottom: 20px;
        .insta-page-link {
            padding-left: 15px;
        }
    }
}
