*:-moz-placeholder {
	color: #a6a6a6;
	opacity: 1;
}

*::-moz-placeholder {
	color: #a6a6a6;
	opacity: 1;
}

*:-ms-input-placeholder {
	color: #a6a6a6;
	opacity: 1;
}

a:active, a:focus {
	outline: none;
}

