html,
body {
	height: 100%;
	padding: 0;
	margin: 0;
}


input:invalid{
    box-shadow:none;
}

body {
	font-size: 16px;
	position: relative; 
	font-family: $roboto-font;
	color: #000;
	-webkit-font-smoothing: antialiased;
}

//sticky footer
.wrapper {
	min-height: 100%;
	margin-bottom: -335px; //equal to footer height
	// overflow-x: hidden;
	&::after {
		content: "";
		display: block;
		height: 335px;
	}
}

.container {
	width: $mainWidth;
	margin: 0 auto;
	&--rel {
		position: relative;
	}
}

.footer {
	height: 335px;
}

* {
	box-sizing: border-box;
}

.img-responsive {
	display: block;
	width: 100%;
	height: auto;
}

ul {
	padding: 0;
	margin: 0;
}

ul li {
	list-style: none;
}

a, a:hover {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-weight: normal;
}

.hide {
    display: none;
    opacity: 0;
    visibility: hidden;
}

.non-visible {
	opacity: 0;
	visibility: hidden;
	overflow: hidden;
	width: 0;
	height: 0;
}

.open {
    display: block !important;
}

.close {
    display: none !important;
}