#cboxNext, #cboxPrevious {
    top: 50%;
    margin-top: -9px;
    width: 18px;
    height: 18px;
    background: url('../img/icon/bottom-arrow.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain; 
    transition: .3s;
    &:hover {
        opacity: .6;
        background-position: center;
    }
    &:hover,
    &:active,
    &:focus {
        outline: none;
    }
}

#cboxNext {
    right: 15px;
    transform: rotate(-90deg);
}

#cboxPrevious {
    left: 15px;
    transform: rotate(90deg);
}

#cboxClose {
    width: 16px;
    height: 16px;
    top: 15px;
    right: 15px;
    background: url('../img/icon/сross_icon.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: .3s;
    &.show {
        opacity: 1 !important;
        visibility: visible !important;
    }

    &:hover {
        opacity: .6 !important;
    }
    &:hover,
    &:active,
    &:focus {
        outline: none;
    }
}

#cboxLoadedContent {
    background: #fff !important;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 !important;
    img {
        margin: auto !important;
    }
}

.cboxIframe {
    padding: 50px;
    background: transparent;
    max-width: 80%;
    max-height: 80%;
}