.main-form {
    &__input {
        width: 100%;
        color: map_get($gray, default);
        font-size: 19px;
        font-weight: 400;
        border: none;
        outline: none;
        padding-bottom: 6px;
        padding-right: 30px;
        border-bottom: 1px solid  map_get($gray-light, hover);
        transition: border .3s;
        &.non-empty-input {
            border-bottom: 1px solid map_get($gray-light, active);
            color: map_get($gray, default);
            & + .form-field-placeholder {
                transform: scale(.90) translateY(-25px);
                span {
                    font-size: 12px;
                }
            }
        }
    }
}

.main-form__row {
    text-align: left;
    max-width: 680px;
    margin: 0 auto;
    &--float {
        @include clearfix;
        .form-field-wrap {
            width: 320px;
            &:first-child {
                float: left;
            }
            &:last-child {
                float: right;
            }
        }
    }
    &--50 {
        width: 50%;
    }
    &:first-child {
        margin-bottom: 50px;
    }
}
.main-form__input--name, .main-form__input--email {
    margin-right: 0;
}

.form-field-wrap {
    position: relative;
    &--email {
        display: inline-block;
        width: 320px;
        margin-right: 25px;
        vertical-align: bottom;
    }
    &--name {
        display: inline-block;
        width: 320px;
        margin-right: 40px;
        vertical-align: bottom;
    }
    &--message {
        width: 100%;
    }
    &.required {
        &::after {
            content: "*";
            display: inline-block;
            color: red;
            position: absolute;
            right: 0;
            top: 0;
        }
    }
    .form-field-placeholder {
        position: absolute;
        bottom: 10px;
        margin-top: 15px;
        transition: transform .4s, color .4s;
        transform-origin: bottom left;
        span {
            color: #a6a6a5;
            font-size: 18px;
            font-weight: 400;
            transition: padding 300ms;
            // text-transform: uppercase;
            font-family: sans-serif;
        }
    }
}
