.footer {
    border-top: 1px solid #b7b7b7;
    padding-top: 25px;
    // padding-bottom: 10px;
    &__top {
        display: flex;
        justify-content: space-between;
        padding-bottom: 30px;
    }
    &__bottom {
        border-top: 1px solid #b7b7b7;
        height: 30px;
        line-height: 30px;
    }
    &__start {
        width: 37%;
    }
    &__end {
        display: flex;
        // width: 430px;
        justify-content: space-between;
    }
    &__end-col {
        flex-grow: 1;
        &:last-child {
            padding-left: 15px;
        }
    }
    .working-hours {
        display: inline-block;
        padding-left: 10px;
    }
    .callback {
        padding-right: 0;
        &__phone {
            font-size: 16px;
        }
        &__title {
            position: static;
            display: inline-block;
            font-size: 14px;
        }
    }
    .email {
        padding-left: 0;
    }
}

.footer__middle {
    margin-right: 60px;
}

.footer-menu {
    &-title {
        color: #2b2b2b;
        font-size: 18px;
        font-weight: 700;
        text-transform: uppercase;
        font-family: $merri-font;
        margin-bottom: 20px;
    }
    &__link {
        font-size: 16px;
        font-weight: 400;
        color: map_get($gray, default);
        display: inline-block;
        padding: 8px 0;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
    }
}

.footer-logo {
    margin-bottom: 30px;
    &__link {
        display: block;
        width: 142px;
        height: 144px;
        margin: 0 auto;
    }
}

.footer-main-menu {
    margin-bottom: 20px;
    max-width: 190px;
    &__link {
        font-size: 16px;
        font-weight: 400;
        color: map_get($gray, default);
        display: block;
        padding: 8px 0;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
    }
}

.footer-nav-list {
    margin-bottom: 30px;
    max-width: 210px;
    &__link {
        font-size: 16px;
        font-weight: 400;
        color: map_get($gray, default);
        display: block;
        padding: 8px 0;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
        &--competition {
            position: relative;
            color: map_get($blue, default);
            &:hover {
                color: map_get($blue, hover);
            }
            &:active {
                color: map_get($blue, active);
            }
            .competition-icon {
                width: 10px;
                height: 15px;
                display: block;
                position: absolute;
                background-size: 0;
                background-image: url(data:image/svg+xml;base64,);
                top: 50%;
                margin-top: -10px;
                left: -15px;
                svg {
                    display: block;
                    max-width: 100%;
                    max-height: 100%;
                    .st0 {
                        transition: fill .3s;
                        fill: map_get($blue, default);
                    }
                }
            }
        }
    }
}

.container--flex {
    display: flex;
    justify-content: space-between;
    // align-items: center;
}

.copyright {
    font-size: 12px;
    font-weight: 400;
    color: map_get($gray-light, default);
}

.development-by {
    font-size: 12px;
    font-weight: 400;
    color: map_get($gray-light, default);
    &__link {
        line-height: 15px;
        margin-left: 5px;
        padding-left: 20px;
        position: relative;
        display: inline-block;
        color: map_get($gray-light, default);
        text-decoration: underline;
        transition: .3s;
        &:hover {
            color: map_get($gray-light, hover);
            text-decoration: underline;
        }
        &:active {
            color: map_get($gray-light, active);
            text-decoration: underline;
        }
        &::before {
            width: 12px;
            height: 12px;
            left: 0;
            top: 50%;
            margin-top: -6px;
            display: inline-block;
            content: '';
            position: absolute;
            background: url('../img/icon/logo_moohii.svg') center center no-repeat;
        }
    }
}