.view-switch {
    &__title {
        font-size: 14px;
        font-weight: 400;
        color: map_get($gray, default);
        vertical-align: text-top;
    }
    &__btns {
        display: inline-block;
    }
    &__btn {
        display: inline-block;
        padding-bottom: 6px;
        width: 45px;
        text-align: center;
        border-bottom: 3px solid transparent;
            svg {
                .st1 {
                    fill: map_get($gray-light, default);
                }
            }
        &--active {
            border-bottom: 3px solid map_get($gray, default);
            svg {
                .st1 {
                    fill: map_get($gray, default);
                }
            }
        }
    }
    &__icon {
        display: inline-block;
        background-size: 0;
        background-repeat: no-repeat;
        svg {
            display: block;
            max-width: 100%;
            max-height: 100%;
        }
        &--table {
            background-image: url(data:image/svg+xml;base64,);            
        }
        &--list {
            background-image: url(data:image/svg+xml;base64,);            
        } 
    }
}