.hint-link {
    cursor: pointer;
    transition: color .3s;
    display: inline-block;
    color: map_get($gray-light, default);
    &:hover {
        color: map_get($gray-light, hover);
    }
    &:active {
        color: map_get($gray-light, active);
    }
    &:hover + .hint-block {
        visibility: visible;
        opacity: 1;
        // display: block;
    }
}

.hint-block {
    width: 340px;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
    padding: 30px;
    position: absolute;
    background: #fff;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s, opacity .3s;
    z-index: 1;
    text-align: left;
    color: map_get($gray-light, default);
    font-size: 14px;
    &.show {
        visibility: visible;
    }
    &__arrow {
        width: 0;
        height: 0;
        border-style: solid;
        position: absolute;
        border-color: #fff;
        margin: 5px;
    }
}

.own-set__hint {
    width: 100%;
    height: auto;
    // margin: 0 auto;
}
.own-set__hint-pic {
    display: block;
    max-width: 100%;
    height: auto;
    object-fit: cover;
}

.hint-block.hint-block--own-set {
    width: 260px;
    padding: 10px;
}

.hint-block[x-placement^="top"] {
    margin-bottom: 5px;
}

.hint-block[x-placement^="top"] .hint-block__arrow {
    border-width: 5px 5px 0 5px;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    bottom: -5px;
    left: calc(50% - 5px);
    margin-top: 0;
    margin-bottom: 0;
}

.hint-block[x-placement^="bottom"] {
    margin-top: 5px;
}

.hint-block[x-placement^="bottom"] .hint-block__arrow {
    border-width: 0 5px 5px 5px;
    border-left-color: transparent;
    border-right-color: transparent;
    border-top-color: transparent;
    top: -5px;
    left: calc(50% - 5px);
    margin-top: 0;
    margin-bottom: 0;
}

.hint-block[x-placement^="right"] {
    margin-left: 5px;
}

.hint-block[x-placement^="right"] .hint-block__arrow {
    border-width: 5px 5px 5px 0;
    border-left-color: transparent;
    border-top-color: transparent;
    border-bottom-color: transparent;
    left: -5px;
    top: calc(50% - 5px);
    margin-left: 0;
    margin-right: 0;
}

.hint-block[x-placement^="left"] {
    margin-right: 5px;
}

.hint-block[x-placement^="left"] .hint-block__arrow {
    border-width: 5px 0 5px 5px;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    right: -5px;
    top: calc(50% - 5px);
    margin-left: 0;
    margin-right: 0;
}