@import '../vars';

.mini-basket {
    position: absolute;
    right: 0;
    z-index: 1001;
    width: 470px;
    background: #fff;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
    padding: 25px 25px 25px 15px;
    display: none;
    // margin-top: 10%;
}
.mini-basket-table {
    width: 100%;
    box-sizing: content-box;
    border-collapse: collapse;
    margin-left: 8px;
    border-spacing: 0;
    max-height: 305px;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 8px;
        background-color: #f5f5f5;
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(#f06292, 0.5);
        border-radius: 10px;
        &:hover {
            background-color: rgba(#f06292, 1);
        }
    }


    &__tr {
        border-bottom: 1px solid #ececec;
        &:first-child {
            td {
                padding-top: 0;
            }
        }
    }
    &__td {
        // vertical-align: middle;
        padding-top: 25px;
        padding-bottom: 20px;
        // border: 1px solid #e1e1e1;
    }
    .product-title {
        max-width: 175px;
        padding-right: 15px;
        &__link {
            display: block;
            line-height: 16px;
            font-size: 12px;
            font-weight: 700;
            color: map_get($gray, default);
            &:hover {
                color: map_get($gray, hover);
            }
            &:active {
                color: map_get($gray, active);
            }
        }
    }

    .product-pic {
        padding-right: 15px;
        &__link {
            display: block;
            max-width: 60px;
            max-height: 50px;
            margin: 0 auto;
        }
    }

    .product-quant-conrol {
        padding-right: 15px;
    }
}


.quant-conrol {
    background-color: #f7f7f7;
    width: 68px;
    height: 30px;
    display: flex;
    justify-content: center;
    .qty-button {
        position: relative;
        align-items: center; // background-color:map_get($brown, default);
        color: map_get($gray, default);
        display: flex;
        justify-content: center;
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
        // width: 20px;
        cursor: pointer;
        padding: 0 7px;
        transition: .3s;
        &:hover {
            color: #79a626;
        }
        &-minus {
            &::before {
                content: '';
                display: block;
                position: absolute;
                background-color: #ececec;
                width: 2px;
                height: 70%;
                right: 0;
                top: 50%;
                transform: translate(0, -50%);
            }
        }
        &-plus {
            &::before {
                content: '';
                display: block;
                position: absolute;
                background-color: #ececec;
                width: 2px;
                height: 70%;
                left: 0;
                top: 50%;
                transform: translate(0, -50%);
            }
        }
    }
    
    .qty {
        border: 0;
        margin: 0;
        padding: 0;
        background-color: #f7f7f7;
        font-size: 14px;
        line-height: 20px;
        font-weight: 700;
        color: map_get($gray, default);
    }
    .qty {
        text-align: center;
        width: 22px;
        outline: none;
    }

    &.quant-conrol--small {
        width: 48px;
        height: 20px;
        background: transparent;
        .qty-button {
            padding: 0 5px;
            &:before {
                display: none;
            }
        }
        .qty {
            width: 20px;
            background: inherit;
        }
    }
    &.quant-conrol--big {
        width: 100px;
        height: 42px;
        .qty-button {
            padding: 0 13px;
            font-size: 22px;
            font-weight: 400;
        }
        .qty {
            width: 30px;
        }
        .qty-button, .qty {
            font-size: 22px;
            font-weight: 400;
        }
    }
}



.product-price {
    font-size: 14px;
    font-weight: 700;
    color: #2b2b2b;
    padding-right: 15px;
    &__new {
        color: #f44336;
       
    }
    &__old {
        text-decoration: line-through;
    }
}

.product-remove {
    &__link {
        display: block;
        width: 18px;
        height: 18px;
        margin: 0 auto;
        &:hover {
            svg {
                .st0 {
                    fill: map_get($red-pale, hover);
                }
            }
        }
        &:active {
            svg {
                .st0 {
                    fill: map_get($red-pale, active);
                }
            }
        }
    }
    &__icon {
        display: block;
        background-image: url(data:image/svg+xml;base64,);
        background-size: 0;
        svg {
            display: block;
            max-width: 100%;
            max-height: 100%;
            .st0 {
                transition: .3s;
                fill: map_get($gray-light, default);
            }
        }
    }
}

.minibasket-subtotal {
    text-align: right;
    margin-top: 20px;
    margin-bottom: 30px;
    &__text {
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        color: #a6a6a6;
    }
    &__amount {
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        color: #2b2b2b;
    }
}