.account-popup {
    padding: 55px 70px;
    &__content {
        width: 335px;
    }
    &__title {
        color: #2b2b2b;
        
        font-weight: 700;
        line-height: 20px;
        &--big {
            font-size: 24px;
        }
        &--small {
            font-size: 22px;
            text-align: center;
        }
    }
    &__hint {
        color: #a6a6a5;
        font-size: 16px;
        font-weight: 400;
        margin-top: 40px;
        text-align: center;
    }
    &__form {
        margin-top: 20px;
        .input-field {
            margin-top: 20px;
        }
    }

    .forgot-pass-link {
        color: map_get($blue, default);
        font-size: 14px;
        font-weight: 700;
        display: inline-block;
        margin-top: 15px;
        transition: .3s color;
        &:hover {
            color: map_get($blue, hover);
        }
        &:active {
            color: map_get($blue, active);
        }
    }

    .blue-btn {
        width: 100%;
        margin-top: 20px;
        height: 45px;
        line-height: 45px;
    }

    .confirm-sign-up-btn {
        line-height: 39px;
        font-size: 18px;
    }

    .socials {
        margin-top: 20px;
        text-align: center;
        &__list {
            display: inline-block;
            vertical-align: middle;
            font-size: 0;
        }
        &__link {
            width: 32px;
            height: 32px;
        }
        &__item {
            margin-right: 15px;
        }
        &__title {
            color: #2b2b2b;
            font-size: 14px;
            font-weight: 400;
            margin-right: 20px;
        }
    }
}

