@import 'vars';

$prod-anim-time: .4s;

.product-block {
    width: 330px;
    margin-top: -50px;
    margin-bottom: -50px;
    float: left;  
    position: relative;
    &:hover {
        .product-block__inner {
            position: absolute;
            z-index: 1;
            background: #fff;
            box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.23); 
            transition: box-shadow $prod-anim-time, 
                        background $prod-anim-time;
            .product-block__hidden-block {
                visibility: visible;
                opacity: 1;
                height: auto;
                margin-top: 30px;
                transition: visibility $prod-anim-time, 
                            opacity $prod-anim-time, 
                            height $prod-anim-time;
            }
        }
    }
    /*&:not(:hover) {
        .product-block__hidden-block {
            margin-top: 30px;
        }
    }*/
    .product-block__inner {
        padding: 50px 30px;
        box-sizing: content-box;
        .product-block__sticker {
            position: absolute;
            left: 30px;
            top: 20px;
            padding: 0 25px;
            text-align: center;
            color: #fff;
            font-family: $roboto-font;
            font-size: 14px;
            font-weight: 400;
            height: 30px;
            line-height: 30px;
            vertical-align: middle;
            &.sales {
                background-color: #fe6459;
            }
            &.top-sales {
                background-color: #efbd52;
            }
        }
        .product-block__img-block {
            display: block;
            width: 270px;
            height: 210px;
            & > img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .product-block__name {
            margin-top: 13px;
            display: block;
            color: map_get($blue, default);
            font-family: $roboto-font;
            font-size: 16px;
            font-weight: 700;
            transition: color .3s;
            &:hover {
                color: map_get($blue, hover);
            }
            &:active {
                color: map_get($blue, active);
            }
        }
        .product-block__price-block {
            margin-top: 10px;
            .price-block_col {
                display: inline-block;
                & > * {
                    cursor: default;
                }
                .price-block__title {
                    font-size: 11px;
                }
                .price-block__title,
                .price-block__price {
                    color: #a6a6a6;
                    font-family: $roboto-font;
                    font-weight: 400;
                }
                .price-block__price-old {
                    margin-top: 10px;
                }

                &.default {
                    margin-right: 15px;
                    .price-block__price {
                        color: map_get($gray, default);
                        font-family: $roboto-font;
                        font-size: 18px;
                        font-weight: 700;
                        &.new {
                            color: #f44336;
                        }
                        &.old {
                            text-decoration: line-through;
                            font-weight: 400;
                        }
                    }
                }

                &.for-signed-up {
                    margin-top: 10px;
                    .price-block__price {
                        font-size: 16px;
                    }
                    .price-block__title {
                        color: map_get($gray-light, default);
                        font-size: 11px;
                        font-style: italic;
                    }
                }
            }
        }
        .product-block__hidden-block {
            visibility: hidden;
            opacity: 0;
            height: 0;
        }
        .product-block__btns-block {
            @include clearfix;
            .add-to-cart-btn {
                float: left;
            }
            .add-to-wishlist-btn {
                float: right;
            }
        }
    }
}