.offer {
    background-color: #fff;
    @include clearfix;
    margin-bottom: 25px;
    position: relative;
    .offer__left-block {
        padding: 35px 60px;
        float: left;
        max-width: 50%;
        .offer__date {
            color: #2b2b2b;
            font-family: $roboto-font;
            font-size: 16px;
            font-weight: 400;
            display: inline-block;
        }
        .offer__classes-type {
            font-family: Roboto;
            font-size: 14px;
            font-weight: 400;
            line-height: 20px;
            display: inline;
            padding-left: 20px;
            cursor: default;
        }
        .offer__title {
            margin-top: 15px;
        }
        .offer__price {
            color: #2b2b2b;
            font-family: $roboto-font;
            font-size: 16px;
            font-weight: 700;
            margin-top: 20px;
        }
        .offer__description {
            color: map_get($gray, default);
            font-family: $roboto-font;
            font-size: 14px;
            font-weight: 400;
            margin-top: 10px;
        }
        .join-btn {
            margin-top: 35px;
        }
    }
    .offer__right-block {
        position: absolute;
        width: 517px;
        height: 100%;
        right: 0;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}