.main-slider {
    &:not(.main-slider--backg) {
        width: 1200px;
        .main-slider__pagination {
            margin-right: 200px;
        }
        .swiper-slide {
            display: flex;
            justify-content: space-between;
        }
    }
    .swiper-slide {
        display: block;
        position: relative;
    }
    .swiper-slide__text {
        padding: 40px 30px;
        width: 45%;
        .swiper-slide__title {
            margin-bottom: 50px;
            color: #fff;
            font-family: "Frank Ruhl Libre";
            font-size: 50px;
            font-weight: 700;
            line-height: 48px;
        }
        .swiper-slide__desc {
            color: #ffffff;
            font-size: 16px;
            padding-bottom: 30px;
        }
    }
    .main-swiper__img {
        display: block;
        margin: auto;
    }
    .main-swiper__backg-img {
        width: 55%;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    .main-slider__pagination-container {
        width: $mainWidth;
        margin: auto;
        position: absolute;
        bottom: 20px;
        z-index: 1;
        .main-slider__pagination {
            float: right;
        }
    }
}