.close-popup-btn {
    width: 16px;
    height: 16px;
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
    background-image: url('data:image/svg+xml;base64,');
    background-size: 0;
    svg {
        width: 100%;
        height: 100%;
        * {
            transition: fill .3s;
        }
    }
    &:hover svg * {
        fill: #7d7b7b !important;
    }
}

#cboxWrapper {
    border-radius: 0;
}

#cboxClose {
    visibility: hidden;
    opacity: 0;
}