.prod-options {
    &__prod-name {
        color: #2b2b2b;
        font-family: $roboto-font;
        font-size: 16px;
        font-weight: 900;
        // text-transform: uppercase;
    }
    &__title {
        color: #a6a6a3;
        font-family: $roboto-font;
        font-size: 14px;
        font-weight: 400;
    }
    & > * {
        padding: 0 15px;
    }
    &__form {
        margin-top: 15px;
    }
    &__select-wrap {
        display: inline-block;
        &:not(:first-child) {
            margin-left: 10px;
        }
    }
    &__label {
        
    }
    &__select {
        height: 31px;
        width: 160px;
    }
    
    .add-comment-btn-wrap {
        text-align: right;
        // margin: 10px 0 5px 0;
        margin: 10px 0 0 0;
        .add-comment-btn {
            margin-right: 5px;
        }
    }
    .add-to-wishlist-btn .add-to-wishlist-btn__title {
        font-size: 14px;
    }
    .turnaround-time {
        .prod-options__title {
            margin-bottom: 15px;
        }
        .custom-radio-button {
            margin-right: 40px;
        }
    }

    &__date-block {
        display: flex;
        margin-top: 20px;
        padding-bottom: 15px;
        // border-bottom: 1px solid #eee;
        position: relative;
        &::after {
            position: absolute;
            display: block;
            content: '';
            width: 645px;
            // width: calc(97% + 3px);
            height: 1px;
            background-color: #eee;
            bottom: 0;
            right: 0;
        }
        .prod-options__date-title {
            color: #2b2b2b;
            font-family: $roboto-font;
            font-size: 14px;
            font-weight: 700;
            line-height: 22px;
            padding-right: 5px;
            
        }
        .prod-options__date {
            color: #a6a6a6;
            font-family: $roboto-font;
            font-size: 12px;
            line-height: 20px;
            span, i {
                /*display: inline-block;*/
            }
        }
    }

    .open-prod-controls {
        margin-top: 10px;
        padding-bottom: 10px;
        &--border {
        }
    }

    .look-item-desc-link {
        color: map_get($gray, hover);
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        transition: color .3s;
        &:hover {
            color: map_get($blue, hover);
        }
        &:active {
            color: map_get($blue, active);
        }
    }

    .btns-container-center {
        text-align: center;
        margin-top: 5px;
    }
}

.preview-prod-popup {
    .prod-options__date-block::after {
        width: 97%;
    }
    .prod-options .open-prod-controls--border::after {
        width: 97%;
    }
}