.header {
    height: 166px;
    position: relative;
    z-index: 4;
    &--open-menu {
        .menu {
            visibility: visible;
            opacity: 1;
            pointer-events: all;
            
           /*  &__link {
                background: transparent;
            }
            &::after {
                display: block;
                content: "";
                position: absolute;
                background-image: url('../img/menu/menu-substrate.png');
                background-size: cover;
                background-position: center center;
                background-repeat: no-repeat;
                width: 100%;
                height: 262px;
                bottom: 0;
                left: 0;
            } */
        }
        // .submenu {
        //     height: 100%;
        // }
    }

    &-inner-wrap {
        background: #fff;
        padding-top: 5px;
    }
    &-top-middle-logo-wrap {
        display: flex;
        align-items: center;
    }
    &__logo {
        // width: 20%;
        width: 245px;
    }
    &-top-middle-wrap {
        width: 80%;
    }
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .nav-list {
            display: flex;
            width: 49%;
            padding-left: 10px;
            &__item {
                display: block;
                &:first-child {
                    .nav-list__link {
                        padding-left: 0;
                    }
                }
                &:last-child {
                    .nav-list__link {
                        padding-right: 0;
                        padding-left: 26px;
                    }
                }
            }
            &__link {
                display: block;
                font-size: 12px;
                font-weight: 400;
                color: map_get($gray, default);
                padding: 10px 11px;
                transition: color .3s;
                &.active {
                    color: map_get($gray, active);
                }
                &:hover {
                    color: map_get($gray, hover);
                }
                &:active {
                    color: map_get($gray, active);
                }
            }
            &__link_competition {
                position: relative;
                color: map_get($blue, default);
                transition: color .3s;
                &:hover {
                    color: map_get($blue, hover);
                    .competition-icon {
                        svg {
                            .st0 {
                                fill: map_get($blue, hover);
                            }
                        }
                    }
                }
                &:active {
                    color: map_get($blue, active);
                    .competition-icon {
                        svg {
                            .st0 {
                                fill: map_get($blue, active);
                            }
                        }
                    }
                }
                .competition-icon {
                    width: 10px;
                    height: 15px;
                    display: block;
                    position: absolute;
                    background-size: 0;
                    background-image: url(data:image/svg+xml;base64,);
                    top: 50%;
                    margin-top: -10px;
                    left: 10px;
                    svg {
                        display: block;
                        max-width: 100%;
                        max-height: 100%;
                        .st0 {
                            transition: fill .3s;
                            fill: map_get($blue, default);
                        }
                    }
                }
            }
        }
    }
    &__middle {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        &.active {
            z-index: 4;
        }
    }
    &__middle-start {
        padding-left: 10px;
    }
}

.blue-line {
    height: 4px;
    width: 100%;
    background-color: map_get($blue, default);
    position: relative;
    z-index: 2;
}
.animate-header {
    // opacity: 0;
