    position: fixed;
}

.no-animate * {
    transition: none !important;
}

.fixed-header {
    width: 100%;
    height: 80px;
    position: fixed;
    z-index: 999;
    top: 0;
    transform: translateY(0%);
    transition: transform .5s;
    padding-top: 10px;
    .header__bottom {
        padding-bottom: 15px;
    }
    .header__logo {
        width: auto;
        // margin-right: 25px;
        // padding-right: 20px;
        margin-right: 28px;
        position: relative;
        /* &::after {
            content: '';
            display: block;
            position: absolute;
            background-color: #ececec;
            width: 1px;
            height: 100%;
            right: 0;
            top: 0;
        } */
    }
    .logo {
        // width: 110px;
        // height: 60px;
        height: 57px;
        width: 57px;
        margin: 0;
    }
    .header-top-middle-logo-wrap {
        justify-content: flex-end;
        margin-bottom: -52px;
    }
    .header-top-middle-wrap {
        width: 70%;
    }
    .header__middle-start {
        padding-left: 0;
    }
    .search__input {
        width: 305px;
        &.infocus {
            width: 198px;
        }
    }
    &.infocus {
        outline: none;
        width: 355px;
        background-image: none;
    }
    .header__top {
        display: none;
    }
    .main-menu {
        border-top: none;
        .main-menu__list  {
            margin-bottom: 0;
            display: inline-block;
            .main-menu__item:not(.menu-trigger) {
                display: none;
            }

        }
    }
}




