    width: 103px;
    height: 104px;
    margin: 0 auto;
    margin-bottom: 10px;
    display: block;
}

.search {
    // display: inline-block;
    &__input {
        width: 415px;
        border: 1px solid #ececec;
        background-color: #ffffff;
        padding: 11px 40px 11px 20px;
        display: inline-block;
        vertical-align: middle;
        background-image: url(../img/icon/search.png);
        background-repeat: no-repeat;
        background-position: 96% center;
        border-radius: 2px;
        
        &:not(.infocus) {
            transition: width .6s;
        }
        &.infocus {
            outline: none;
            width: 300px;
            background-image: none;
            border-color: rgba(98, 223, 240, 0.3);
            border-radius: 2px;
            transition: width .3s;
        }
    }
    .search__btn {
        visibility: hidden;
        opacity: 0;
        width: 0;
        overflow: hidden;
        &.show {
            width: 114px;
            // display: inline-block;
            visibility: visible;
            opacity: 1;
            transition: opacity .5s .2s,
            visibility .5s .2s, width 0s .2s;

            // animation: scale .5s ease-in-out;
        }
    }
}

@keyframes scale {
    from {
        transform: scale(1);
    }
    50% {
        transform: scale(1.01);
    }
    to {
        transform: scale(1);
    }
}


.signIn-cart {
    &__list {
        display: flex;
        align-items: center;
    }
    &__item {
        display: block;
        position: relative;
        &:last-child {
            // .signIn-cart__link {
            //     padding-right: 30px;
            // }
        }
        &:first-child {
            &::before {
                display: none;
            }
            .signIn-cart__link {
                padding-left: 0;
            }
        }
        &::before {
            content: '';
            display: block;
            position: absolute;
            background-color: #ececec;
            width: 1px;
            height: 75%;
            top: 50%;
            transform: translateY(-50%);
        }
        .wishlist-icon {
            width: 20px;
            height: 18px;
            display: block;
            position: absolute;
            background-size: 0;
            background-image: url(data:image/svg+xml;base64,);
            top: 50%;
            margin-top: -9px;
            left: 15px;
            svg {
                display: block;
                max-width: 100%;
                max-height: 100%;
                .st0 {
                    fill: #fff;
                }
                .st1 {
                    fill: map_get($blue, default);
                }
            }
        }
        .cart-icon {
            width: 26px;
            height: 20px;
            display: block;
            position: absolute;
            background-size: 0;
            top: 50%;
            margin-top: -10px;
            left: 7px;
            background-image: url(data:image/svg+xml;base64,);
            svg {
                display: block;
                max-width: 100%;
                max-height: 100%;
                .st0 {
                    fill: #fff;
                }
                .st1 {
                    fill: map_get($blue, default);
                }
            }
        }
        &.inWishlist { // Добавить класс для "signIn-cart__item" когда в избранных есть товар
            svg {
                .st0 {
                    fill: map_get($blue, default);
                }
                .st1 {
                    fill: map_get($blue, default);
                }
            }
        }
        .inWishlist-number {
            border: 2px solid map_get($blue, default);
            background-color: #ffffff;
            border-radius: 50%;
            position: absolute;
            top: -10px;
            left: 30px;
            color: map_get($blue, default);
            font-size: 11px;
            font-weight: 700;
            padding: 1px 4px;
        }
        &.minicart {
            background-color: #ffffff;
            &::before {
                height: 39%;
            }
            .num-prod-in-cart {
                font-size: 12px;
                font-weight: 700;
                color: map_get($blue, default);
                margin-left: 3px;
            }
        }
        &.minicart--full {
            // box-shadow: 0 1px 20px 1px rgba(0, 0, 0, 0.05);
            // transition: box-shadow ease .2s;
            &::before {
                display: none;
            }
            .cart-icon {
                svg {
                    .st0 {
                        fill: map_get($blue, default);
                    }
                    .st1 {
                        fill: map_get($blue, default);
                    }
                }
            }
            
            &:hover {
                box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
                transition: box-shadow ease .1s;
            }
        }
    }
    &__link {
        display: block;
        font-size: 14px;
        font-weight: 400;
        padding: 5px 15px;
        color: map_get($gray-light, default);
        transition: color .3s;
        &--cart {
            width: 185px;
            padding: 17px 0 17px 40px;
        }
        &--wishlist {
            padding-left: 40px;
        }
        &:hover {
            color: map_get($gray-light, hover);
        }
        &:active {
            color: map_get($gray-light, active);
        }
    }
}



.main-menu {
    &__item {
        
        display: block;
    }
}

.main-menu {
    border-top: 1px solid #ececec;
    // border-bottom: 4px solid #f06292;
    &__list {
        margin-bottom: -4px;
        display: flex;
        justify-content: space-between;
    }
    &__item {
        position: relative;
        flex-grow: 1;
        &::before {
            content: '';
            display: block;
            position: absolute;
            background-color: #ececec;
            width: 1px;
            height: 65%;
            top: 47%;
            transform: translateY(-50%);
        }
        &:last-child {
            .main-menu__link {
                padding-right: 35px;
                text-align: right;
            }
            &::before {
                left: 5px;
            }
        }
    }
    &__link {
        background: #fff;
        display: block;
        padding: 15px 15px 10px 15px;
        font-family: $merri-font;
        font-size: 15px;
        font-weight: 700;
        color: map_get($gray, default);
        text-align: center;
        transition: color .3s;
        &.active {
            color: map_get($blue, active);
        }
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
    }
    .menu-trigger {
        width: 245px;
        margin-top: -1px;
        // position: static;
        flex-grow: 0;
        &:hover {
            .menu {
                opacity: 1;
                visibility: visible;
                pointer-events: all;
            }
        }
        &::before {
            display: none;
        }
        .main-menu__link {
            display: block;
            padding: 15px 0;
            text-align: center;
            font-family: $merri-font;
            font-size: 15px;
            font-weight: 700;
            color: #ffffff;
            background-color: map_get($blue, default);
            position: relative;
            padding-left: 30px;
        }
    }
    
}

.hamburger {
    display: block;
    left: 15px;
    top: 48%;
    transform: translate(0, -50%);
    position: absolute;
    .line {
        width: 20px;
        height: 2px;
        background: #fff;
        margin-top: 3px;
        display: block;
        border-radius: 6px;
        &:first-child {
            margin-top: 0;
        }
    }
}

.menu {
    width: inherit;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
    background-color: #ffffff;
    position: absolute;
    left: 0;
    top: 46px;
    z-index: 99;
    padding-top: 10px;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: .3s;
    height: 427px;
    &:hover {
        &::before {
            box-shadow: inset 0 1px 18px 3px rgba(29, 25, 41, 0.13);
        }
    }
    &::before {
        display: block;
        content: "";
        position: absolute;
        height: 100%;
        // width: calc(100% - 15px);
        width: 100%;
        background-color: #fff;
        top: 0;
        z-index: 1;
        
    }
    &::after {
        display: block;
        content: "";
        position: absolute;
        background-image: url('../img/menu/menu-substrate.png');
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        width: 100%;
        height: 262px;
        bottom: 0;
        left: 0;
        z-index: 1;
    }
    &.closed {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
    &__item {
        display: block;
        /* &:first-child .menu__link:before {
            content: '';
            position: absolute;
            left: 0;
            top: -10px;
            background: #fff;
            width: 100%;    
            height: 10px;
            z-index: 1;
        } */
        &:hover {
            .submenu {
                opacity: 1;
                visibility: visible;
                z-index: -1;
                transform: translate(0);
            }
            .menu__link {
                color: map_get($blue, default);
                font-weight: 700;
                background-color: #f7f7f7;
            }
            .menu__link-icon.arrow-right {
                svg {
                    opacity: 1;
                }
            }
        }
    }
    &__link {
        display: block;
        position: relative;
        padding: 7px 50px;
        color: map_get($gray, default);
        font-weight: 400;
        // background: #fff;
        z-index: 2;
        transition: background .3s, color .3s;
        &-icon.arrow-right {
            background-image: url(data:image/svg+xml;base64,);
            width: 7px;
            height: 10px;
            display: block;
            position: absolute;
            background-size: 0;
            right: 15px;
            top: 50%;
            margin-top: -5px;
            svg {
                display: block;
                max-width: 100%;
                max-height: 100%;
                fill:#a7a7a7;
                opacity: 0.3;
                transition: .3s;
            }
        }
    }
}

.submenu {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    width: 480px;
    height: 100%;
    top: 0;
    right: -480px;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
    background-color: #ffffff;
    transition: transform .3s .2s,
                opacity .3s .2s,
                visibility .3s .2s;
    transform: translate(-245px);
    &__title {
        font-weight: 700;
        text-transform: uppercase;
        color:  map_get($blue, default);
        font-size: 16px;
        padding: 20px 40px 10px;
    }
    &__list {
        column-count: 2;
        column-rule: 1px solid #ececec;;
        column-gap: 0;
        padding-bottom: 10px;
    }
    &__item {
        display: block;
        &:hover a {
            background-color: #f7f7f7;
        }
    }
    &__link {
        display: block;
        font-size: 16px;
        font-weight: 400;
        color:  map_get($gray, default);
        padding: 10px 0;
        transition: color .3s;
        padding-left: 40px;
        &:hover {
            color: map_get($blue, default);
            font-weight: 700;
        }
    }
}

