@import 'vars';

.reset-btn-style {
    border: none;
    background: transparent;
    outline: none;
    cursor: pointer;
    padding: 0;
}

.add-to-cart-btn,
.add-to-wishlist-btn {
    @extend .reset-btn-style;
    display: inline-block;
    line-height: 18px;
    svg  {
        .st0 {
            fill: #fff;
        }
        .st1 {
            fill: map_get($blue, default);
        }
    }

    &:hover {
        // color: map_get($pink, default);
        svg  {
            .st0 {
                fill: map_get($blue, default);
            }
            .st1 {
                fill: map_get($blue, default);
            }
        }
    }
    .add-to-cart-btn__icon,
    .add-to-wishlist-btn__icon {
        background-size: 0;
        display: inline-block;
        vertical-align: middle;
        svg {
            width: 100%;
            height: 100%;
            * {
                transition: fill .2s;
            }
        }
    }
    .add-to-cart-btn__title,
    .add-to-wishlist-btn__title {
        color: map_get($gray, default);
        font-family: $roboto-font;
        vertical-align: middle;
        font-weight: 700;
        transition: color .3s;
    }
}

.add-to-cart-btn {
    .add-to-cart-btn__icon {
        background-image: url(data:image/svg+xml;base64,);
        width: 22px;
        height: 18px;
    }
    .add-to-cart-btn__title {
        font-size: 16px;
    }
}

.add-to-wishlist-btn {
    .add-to-wishlist-btn__icon {
        background-image: url(data:image/svg+xml;base64,);
        width: 16px;
        height: 14px;
    }
    .add-to-wishlist-btn__title {
        font-size: 16px;
    }
}

.see-all-link {
    color: map_get($blue, default);
    font-family: $roboto-font;
    font-size: 14px;
    font-weight: 400;
    text-decoration: underline;
    transition: color .3s;
    // @extend .reset-btn-style;
    display: inline-block;
    &:hover {
        color: map_get($blue, hover);
        text-decoration: underline;
    }
    &:active {
        color: map_get($blue, active);
        text-decoration: underline;
    }
}

.animate-btn {
    position: relative;
    overflow: hidden;
    &:hover {
        background: map_get($blue, default) !important;
        &:after {
             width: 150%;
             opacity: 1;
        }
    }
    &:active {
        background: map_get($blue, default) !important;
        &:after {
            background: map_get($blue, active);
        }
    }
    span {
        position: relative;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 2;
    }
    &:after {
        content: "";
        position: absolute;
        background: map_get($blue, hover);
        height: 100%;
        width: 0;
        left: 0;
        top: 0;
        z-index: 1;
        transition: all .5s ease 0s;
        opacity: .5;
        // left: 50%;
        // top: 50%;
        // transform: translateX(-50%) translateY(-50%);
    }
}

.blue-btn {
    height: 35px;
    padding: 0 30px;
    color: #fff;
    font-family: $roboto-font;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    border-radius: 2px;
    display: inline-block;
    line-height: 35px;
    vertical-align: middle;
    text-align: center;
    background: map_get($blue, default);
    transition: background .3s;
    @extend .reset-btn-style;
    @extend .animate-btn;
    &:hover {
        background: map_get($pink, hover);
    }
    &:active {
        background: map_get($pink, active);
    }
}

.promo-btn {
    height: 38px;
    padding: 0 30px;
    color: #fff;
    font-family: $roboto-font;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    border-radius: 3px;
    display: inline-block;
    line-height: 38px;
    vertical-align: middle;
    text-align: center;
    background-color: #a6a6a6;
    transition: background .3s;
    @extend .reset-btn-style;
    /* @extend .animate-btn;
    &:hover {
        background: map_get($pink, hover);
    }
    &:active {
        background: map_get($pink, active);
    } */
}

.border-blue-btn {
    height: 35px;
    padding: 0 30px;
    color: map_get($gray, default);
    font-family: $roboto-font;
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    border-radius: 3px;
    display: inline-block;
    line-height: 35px;
    vertical-align: middle;
    text-align: center;
    border: 2px solid map_get($blue, default);
    transition: background .3s, color .3s, border .3s;
    @extend .reset-btn-style;
    &:hover {
        color: #fff;
        background: map_get($blue, default);
    }
    &:active {
        color: #fff;
        background: map_get($blue, active);
        // border: 2px solid map_get($blue, active);
    }
}

.blue-btn--search {
    @extend .border-blue-btn;
    height: 42px;
    line-height: 42px;
    padding: 0;
    width: 114px;
}
.blue-btn--checkout {
    @extend .blue-btn;
    height: 42px;
    line-height: 42px;
    width: 195px;
    display: block;
    margin: 0 auto;
}
.blue-btn--place-order {
    @extend .blue-btn;
    height: 42px;
    line-height: 42px;
}
.blue-btn--form {
    @extend .border-blue-btn;
    padding: 0 25px;
    height: 42px;
    line-height: 42px;
}

.dashed-btn {
    @extend .reset-btn-style;
    display: inline-block;
    text-align: center;
    border-radius: 3px;
    border: 1px dashed #7a7a7a;
    background-color: #fff;
    color: #2b2b2b;
    font-size: 14px;
    font-weight: 400;
    height: 40px;
    line-height: 40px;
    padding: 0 23px;
    cursor: pointer;
    transition: color .3s;
    &:hover {
        color: map_get($blue, hover);
    }
    &:active {
        color: map_get($blue, active);
    }
}

.add-comment-btn {
    @extend .dashed-btn;
    padding: 0 15px;
    height: 28px;
    font-size: 12px;
    font-weight: 700;
    line-height: 20px;
}

.load-more-btn {
    @extend .dashed-btn;
    background: #fff;
    font-size: 14px;
    // width: 150px;
    height: 35px;
    line-height: 35px;
}

.buy-in-click-btn {
    color: #2b2b2b;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 700;
    display: inline-block;
    transition: color .3s;
    &:hover {
        color: map_get($blue, default);
    }
    &:active {
        color: map_get($blue, active);
    }
}

