.gray-btn {
    @extend .reset-btn-style;
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
    height: 30px;
    line-height: 30px;
    border-radius: 2px;
    display: inline-block;
    color: #fff;
    background-color: map_get($gray-light, default);
}

.btn--green {
    background: map_get($green, default);
    &:after {
        background: map_get($green, hover);
    }
    &:active {
        background:  map_get($green, active);
        &:after {
            background:  map_get($green, active);
       }
    }
}


.calendar-btn {
    @extend .reset-btn-style;
    padding: 12px 15px 10px;
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    height: 40px;
    vertical-align: middle;
    display: inline-block;
    color: #fff;
    border-radius: 3px;
    background-color: #a6a6a6;
    background-repeat: no-repeat;
    width: 150px;
    height: 40px;
    padding-left: 50px;
    background-position: 15px center;
    background-image: url("../img/icon/calendar.png")
}

